import { Injectable } from '@angular/core';
import { CustomUser } from '../models/user-class';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { ConfigService } from './config.service';
import { catchError, tap } from 'rxjs/operators';
import { error } from 'console';
import { NbTokenLocalStorage } from '@nebular/auth';

@Injectable({
  providedIn: 'root'
})

/**
 * Service de gestion des utilisateurs.
 * Permet d'avoir des infos sur la liste des utilisateurs ou sur l'utilisateur courant.
 */
export class UserMgrService {
  currentUser: CustomUser; // utilisateur courant
  userList: CustomUser[];  // liste des utilisateurs
  httpOptions: any; // en-tte HTTP utilis pour les requtes


  constructor(
    private http: HttpClient,
    private config: ConfigService,
    // public tokenStorage: NbTokenLocalStorage,
    ) { 
    this.httpOptions = {
      headers: new HttpHeaders({ 'Content-type': 'application/json'}),
    };

    //this.currentUser = new CustomUser('manager');
    // this.currentUser = new CustomUser('guest');
    // this.currentUser = 
    const storedUser = this.getStoredUser();
    if(storedUser){
      this.currentUser = storedUser;
      console.log('found user', storedUser)
    }
    else {
      this.currentUser = new CustomUser('guest');
    }
  }

  getStoredUser(){
    console.log(localStorage.getItem('eDCPLoggedUser'));
    return JSON.parse(localStorage.getItem('eDCPLoggedUser'));
  }

  setStoredUser(){
    console.log('storing user: ', JSON.stringify(this.currentUser));
    localStorage.setItem('eDCPLoggedUser', JSON.stringify(this.currentUser));
  }

  clearStoredUser(){
    console.log('deleting stored user: ', localStorage.getItem('eDCPLoggedUser'));
    localStorage.removeItem('eDCPLoggedUser');

  }

  /**
   * 
   * @returns Observable de la liste des utilusateurs de type CustomUser
   */
  getUserList(): Observable<CustomUser[]>{
    return this.http.get<CustomUser[]>(this.config.apiUrl.users).pipe(
      tap( users => console.log(`User list ok : ${users}`) ),
      catchError( error => {
        console.log(`User list error ${error}`);
        return of([]);
      }),
    );
  }

  /**
   * 
   * @param id : id de l'utilisateur  rechercher
   * @returns : Observable de l'utilisateur (class CustomUser)
   */
  getUserbyId(id: string): Observable<CustomUser>{
    console.log(`Getting user by Id ${id}`);
    return this.http.get<CustomUser>(this.config.apiUrl.users + id).pipe(
      tap( user => {
        console.log(`User : ${user}`);
        this.currentUser = user;
        this.setStoredUser();
      }),
      catchError( error => {
        console.log(`User error ${error}`);
        return of();
      }),
    );
  } 

  getUserbyIdAlt(id: string) {
    console.log(`Getting user by Id ${id} from : ${this.config.apiUrl.users + id}`);
    this.http.get<CustomUser>(this.config.apiUrl.users + id).subscribe(
      (user) => {
        console.log('user ok !');
        console.log(user);
        this.currentUser = user;
      },
      (error) => {
        console.log(error);
      }
    );

    /* if(this.currentUser.id){
      console.log('redirecting to dashboard');
      return true;
    }
    console.log('waiting...');
    return false; */
  } 
}
